      font-size: 18px; } }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

svg path {
  fill: inherit; }

a {
  text-decoration: none; }

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle; }

@font-face {
  font-family: "DidactGothic-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src: local("DidactGothic-Regular"), url("../fonts/DidactGothic-Regular.woff2") format("woff2"), url("../fonts/DidactGothic-Regular.woff") format("woff"); }

@font-face {
