  font-style: italic;
  color: #a0969e; }
  @media (min-width: 768px) {
    .quote {
      margin-bottom: 0; } }

.skills__container {
  display: flex;
  flex-direction: column; }
  @media (min-width: 768px) {
    .skills__container {
      flex-direction: row; } }

@media (min-width: 768px) {
  .skills__col {
    width: calc(33.3333%);
    margin: 0 15px 0 0; } }

.socials {
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }
  @media (min-width: 768px) {
    .socials {
      align-items: center; } }

.socials__link {
  margin-right: 20px;
