  height: 40px; }

.work {
  position: relative;
  margin-bottom: 25px;
  border-radius: 15px;
  background-color: #f7e2d9; }
  .work:nth-child(2) {
    background-color: #F9EBDF; }
  .work:nth-child(3) {
    background-color: #eedddd; }
  .work:nth-child(4) {
    background-color: #E4E4E4; }
  @media (min-width: 768px) {
    .work {
      display: flex; } }
  @media (min-width: 1024px) {
    .work {
      width: calc(50% - 25px);
      margin-bottom: 50px; }
      .work.is-hover {
        z-index: 1; } }

.work__link {
  display: block;
  padding: 30px 30px 0 30px;
  border-radius: 15px;
  text-align: center;
  transition: 0s all 0.3s; }
  @media (min-width: 768px) {
