@media (min-width: 1024px) {
  .work:nth-child(odd) .work__link:hover, .work:nth-child(odd) .work__link:focus {
    border-top-right-radius: 0;
    border-bottom-right-radius: 0; }
  .work:nth-child(odd) .work__container {
