@font-face {
  font-family: "DidactGothic-Regular";
  font-style: normal;
  font-weight: normal;
  font-display: swap;
  src:
    local("DidactGothic-Regular"),
    url("../fonts/DidactGothic-Regular.woff2") format("woff2"),
    url("../fonts/DidactGothic-Regular.woff") format("woff");
}

